//
// IMPORTS
//
import 'babel-polyfill' // required for generator support, for redux-saga

import { Meteor } from 'meteor/meteor'
import React from 'react'
import ReactDOM from 'react-dom'
import { applyMiddleware, compose, createStore } from 'redux'
import { AppContainer } from 'react-hot-loader'

import Root from './react/containers/Root'
import rootReducer from './reducers'

import rootSaga from './sagas'
import createSagaMiddleware from 'redux-saga'

import {sayHello} from './reducers/data'
import {apiRequest} from './utils/api'

//
// SAGA
//

const sagaMiddleware = createSagaMiddleware()

//
// STORE
//

const store = createStore(
  rootReducer,
  compose(
    applyMiddleware(sagaMiddleware),
    window.devToolsExtension
    ? window.devToolsExtension()
    : f => f
  )
)
sagaMiddleware.run(rootSaga)

// APP
Meteor.startup(() => {
  // api demo
  apiRequest('/hello')
  Streamy.on('hello', (hello) => {
    // sayHello(hello.hello)
  })
  // actual render app
  const reactDivElement = document.getElementById('render-target')
  if (reactDivElement) {
    ReactDOM.render(
      <AppContainer>
        <Root store={store} />
      </AppContainer>
    , reactDivElement)
  }
})
